import { PropsWithChildren, useMemo } from 'react';
import { MantineProvider, ColorScheme, ColorSchemeProvider } from '@mantine/core';
import { theme } from './theme';
import { useLocalStorage } from './hooks';
import { lightModeColors, darkModeColors } from './constants/colors';

export const ThemeProvider: React.FC<PropsWithChildren> = ({ children }) => {
  const [colorScheme, setColorScheme] = useLocalStorage<ColorScheme>({
    key: 'tw-ui-color-scheme',
    defaultValue: 'light',
  });

  const toggleColorScheme = () => setColorScheme((prev) => (prev === 'light' ? 'dark' : 'light'));
  const colors = colorScheme === 'dark' ? darkModeColors : lightModeColors;
  const overriddenTheme = useMemo(() => ({ ...theme, colors }), [colors]);

  return (
    <ColorSchemeProvider colorScheme={colorScheme} toggleColorScheme={toggleColorScheme}>
      <MantineProvider {...{ children, colorScheme, theme: overriddenTheme }} />
    </ColorSchemeProvider>
  );
};
